body{
background: #bdc3c7;
background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
background: linear-gradient(to right, #2c3e50, #bdc3c7);
}
.auth-container {
background-color: #2c3e50;
border-radius: 8px;
box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
padding: 20px;
width: 300px;
margin: 0 auto;
margin-top: 50px;
}

.auth-container h2 {
text-align: center;
}

.auth-container form {
display: flex;
flex-direction: column;
gap: 10px;
}
.checkbox2 {
display: flex;
align-items: center;
}

.label2 {
margin: 0;
padding: 0;
display: flex;
color: black;
align-items: center;
flex-direction: row;
}

.auth-container label {
font-weight: bold;
}

.auth-container input {
padding: 8px;
border: 1px solid black;
border-radius: 4px;
box-sizing: border-box;
width: 100%;
}

.auth-container button {
padding: 5px;
margin: 5px;
background-color: #4caf50;
color: black;
font-weight: 800;
border: none;
border-radius: 4px;
cursor: pointer;
}

.auth-container p {
text-align: center;
font-weight: 800;
}

.auth-container a {
color: green;
text-decoration: none;
}

.auth-container a:hover {
text-decoration: underline;
}

.error {
color: #ff0000;
text-align: center;
}

.forgot-password {
    text-align: center;
    margin-top: 10px;
    cursor: pointer;
    }