.details{
position: fixed;
top: 0;
left: 0;
background-color: rgba(0, 0, 0, 0.4);
height: 100%;
width: 100%;
display: flex;
align-items: center;
justify-content: center;
overflow: hidden;
}

.de_detail{
background:rgb(16, 16, 36);
color: white;
max-width: 550px;
width: 100%;
border-radius: 2rem;
box-shadow: 2px 3px 10px blueviolet;
/* min-height: 300px; */
display: flex;
flex-direction: column;
position: relative;
}


.de_title{
padding: 0;
margin: 0px 20px 20px 0px;
border-bottom: 1px solid whitesmoke;
}

.p{
padding-left: 5px;
}

.loc{
margin-right: 5px;
}

.de_content{
flex: 1;
margin: 24px -17px 0px 0px;
}

.de_content ul{
list-style-type: none;
text-align: center;
}

.de_content ul li{
padding:20px;
}

.de_content ul li p{
text-align:start;
margin: 25px;
font-family:'Segoe UI', Tahoma, Verdana, sans-serif;
}

.de_footer{
padding: 0.5rem;
border-top: 1px solid whitesmoke;
margin: 0px 20px -15px 0px;
display: flex;
justify-content: flex-end;
}

.de_close{
position: absolute;
font-size: 1.5rem;
font-weight: bold;
top:0.2rem;
right: 1rem;
cursor: pointer;
margin-top: 8px;
}

.btn {
margin: 2px 2px 0px 7px ;
padding: 10px 15px;
border: 0;
border-radius: 100px;
background-color: rgb(9, 163, 104);
color: rgb(16, 16, 36);
font-weight: Bold;
transition: all 0.5s;
-webkit-transition: all 0.5s;
}

.btn:hover {
color:black;
background-color:rgb(9, 163, 104);
box-shadow: 0 0 20px rgb(9, 163, 104);
transform: scale(1.1);
}

.btn:active {
background-color: #2A2B31;
transition: all 0.25s;
-webkit-transition: all 0.25s;
box-shadow: none;
transform: scale(0.98);
}

.prodimge{
