/* ForgotPassword.css */
.forgot-password-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
    
    .forgotpass {
    display: flex;
    flex-direction: column;
    }
    
    .email1 {
    margin-bottom: 8px;
    }
    
    .email2 {
    padding: 8px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    }
    
    .buttons {
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    }
    
    .buttons:hover {
    background-color: #0056b3;
    }
    