@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

*{
font-family: 'Roboto', sans-serif;
padding:0;
margin: 0;
box-sizing: border-box;
}

.nav{
display: flex;
justify-content: space-around;
align-items: center;
height: 75px;
color: white; 
background-color: rgb(16, 16, 36);
}

.logo{
font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
left: 50px;
}

.navlink{
display: flex;
justify-content: flex-end;
list-style-type: none;
width: 75%;
}


.home{
color:white;
text-decoration: none;
justify-content: space-between;
font-size:x-small;
padding: 10x 20px;
margin: 10px 30px;
cursor: pointer;
}

.home {
border:none;
transition: all 0.3s;
cursor: pointer;
font-size: 17.5px;
padding: 10px;
font-weight:lighter;
font-family: 'Montserrat', sans-serif;
}

.home:hover {
border: 2px solid rgb(13, 158, 102);
color: aqua;
box-shadow: 0 4px 5px;
border-radius: 8px;
text-align: center;
/* text-transform: uppercase; */
font-size:15px;
}

.menuicon{
display:none;
}


@media screen and (max-width:600px) {
.logo{
text-align: start;
display: flex;
position: absolute;
top: 15px;
left: 35px;
}

.navlink{
display: none;
flex-direction: column;
}

.navmob{
position:absolute;
display: flex;
flex-direction: column;
list-style-type: none;
top:70px;
left: 0;
padding:10px;
text-align: center;
width: 100%;
height:max-content;
transition: all 0.5s ease-out;
background-color:black;
}

.navmob li{
margin: 10px 0;
}

.home{
color: rgb(13, 158, 102);
text-align: center;
padding: 12px;
width: 100%;
transition: all 0.5s ease;
}


.menuicon{
display:block;
position: absolute;
font-size: 20px;
color:white;
background-color: rgb(16, 16, 36);
border: none;
top: 26px;
right: 20px;
border: none;
}
}