.admin{
  display: flex;
}

.admin-left{
  width: 20%;
  height: 89vh;
  background-color: transparent;
  backdrop-filter: blur(40px);
  background: #bdc3c7;
  background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
  background: linear-gradient(to right, #2c3e50, #bdc3c7);
  padding: 20px;
  border-radius: 10px;
  border:3px solid #2A2B31;
  text-align: center;
}

.admin-home{
  color: black;
  font-size: 29px;
  font-weight: 700;
}

.ad-leftcon{
  text-align: left;
  margin-top: 30px;
  font-size: large;
  font-weight: 700;

}

.ad-con{
  padding: 10px;
}

.admin-propage{
  text-decoration: none;
  color: black;
}

.admin-right{
  background: #bdc3c7;
  background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);
  background: linear-gradient(to right, #2c3e50, #bdc3c7); 
  background-position:fill;
  background-size:cover;
  color: black;
  width: 80%;
  height: 88.3vh;
  text-align: center;
}

.admin-right h3{
  color: black;
  font-family:'Arial', sans-serif;
  font-size: 16px;
  font-weight: bold;
  text-align: left;
  padding: 10px;
}