background: -webkit-linear-gradient(to right, #2c3e50, #bdc3c7);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #2c3e50, #bdc3c7);
}

.home1{
padding-top: 20px;
overflow:auto;
/* height: 100vh; */
width: 100%;
background-position: center;
background-size: cover;
min-height: 100%;
}

.input {
font-family: "Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans";
font-weight: 450;
font-size: 18px;
color:darkgrey;
background-color:rgb(16, 16, 36);
border-radius: 1.2vw 0  0 1.2vw;
border: none;
outline: none;
padding: 0.4vw;
transition: .4s;
}


.searchbar {
margin-left: -1px;
font-family: "Segoe UI","Roboto","Oxygen","Ubuntu",sans-serif;
font-weight: 500;
font-size: 18px;
color:white;
background-color:rgb(16, 16, 36);
border-radius: 0 1.2vw 1.2vw 0;
border: none;
outline: none;
padding: 0.4vw;
transition: .4s;
}

/* PRODUCT */
.ul{
display: grid;
width: 100%;
grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
list-style-type: none;
margin: 10px;
padding: 10px;
}

.ul li{
justify-content: space-around;
font-family: 'Montserrat', sans-serif;
border: 2px solid black;
background-color: rgb(16, 16, 36);
border-radius: 35px; 
padding: 5px;
color: white;
margin: 0 100px 60px 60px;
text-align:center;
}

.ul li:hover{
box-shadow: 0 0 20px rgb(16, 16, 36);
transform: scale(0.9);
font-weight:550;
}

.cart-icon{
font-size: 10px;
}

.cart {
width: 36%;
border: 1px solid rgb(81, 255, 0);
padding: 5px;
font-family: 'Montserrat', sans-serif;
margin: 10px;
font-size: 10px;
border-radius: 50px;
background-color: rgb(18, 160, 89);
color: black;
font-weight: bold;
transition: all 0.5s;
-webkit-transition: all 0.5s;
}

.cart:hover {
color:black;
background-color: rgb(11, 184, 97);
box-shadow: 0 0 20px #0a7e53;
transform: scale(1.1);
}

.cart:active {
background-color: rgb(11, 184, 97);
transition: all 0.25s;
-webkit-transition: all 0.25s;
box-shadow: none;
transform: scale(0.98);
}

.image{
padding: 3px;
width: 120px;
height: 120px;
} 

.button {
font-size: 10px;
padding: 0.3em 0.3em;
border: transparent;
background:  rgb(0, 50, 0);
color: white;
border-radius: 0px;
margin-top:5px;
text-align: center;
margin-bottom: 5px;
}


.price{
display: flex;
flex-direction: column;
align-items: center;
margin-bottom: -15px;
}

.price1{
font-size: 12px;
text-decoration: line-through;
}

.off{
padding: 2px;
font-size: 16px;

}

.deliv{
margin-bottom: -5px;
color: green;
}


.cartimg{
text-decoration: none;
border: none;
background-color: rgb(16, 16, 36);
}


