.ad-products{
    color: #FEE715;
    margin: 80px;
    margin-top: 40px;
    margin-right: 350px;
    margin-left: 130px;
    background: transparent;
    backdrop-filter: blur(30px);
    border: 3px solid #2A2B31;
    border-radius: 20px;
    box-shadow: 0 1px 14px darkcyan;
}

.ad-products ul{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    list-style-type: none;
    margin: 10px;
    padding: 10px;
}

.ad-products li{
    margin-top: -10px;
}

.ad-input{
    margin: 0 40px 10px 0;
}