.addcart{
height: 100%;
width: 50%;
max-width: 30%;
border: 3px solid black;
background-color: rgb(16, 16, 36);
overflow: hidden;
}

.cart1{
/* border-top: 1px solid black; */
border-bottom: 3px solid black;
padding: 1px 0px 5px 0px;
background-color: rgb(16, 16, 36);
color: white;
display: flex;
>h2 {
margin-left: 10px;
margin-top: 5px;
}
}

.carticon{
margin-left: 220px;
margin-top: 10px;
}

.count1{
border: none;
background-color: white;
text-align: center;
border-radius: 20%;
color:black;
margin-left: 8px;
font-size: 18px;
font-weight:800;
max-width: 30px;
}

.count{
margin-top: 12px;
}

.prods ul {
list-style-type: none;
text-align: center;     
color: black;
}

.prods ul li{
display: flex;
padding:  5px;
/* margin-top:-20px; */
align-items: center;
justify-content: space-around;
border: 1px solid black;
background:rgb(43, 87, 85);
border-radius: 2%;
margin: 2px 2px 2px 2px;
font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
font-weight: 600;
}

.prods ul button {
cursor: pointer;
outline: none;
border: none;
color: black;
}

.prods ul button {
padding: 8px 8px;
border: 0;
border-radius: 100px;
background-color: black;
color: darkgrey;
font-weight: Bold;
transition: all 0.5s;
-webkit-transition: all 0.5s;
}

.prods ul button:hover {
color:black;
background-color: rgb(9, 163, 104);
box-shadow: 0 0 20px darkgrey;
transform: scale(1.1);
}

.prods ul button:active {
background-color:rgb(9, 163, 104);
transition: all 0.25s;
-webkit-transition: all 0.25s;
box-shadow: none;
transform: scale(0.98);
}

.total{
position: sticky;
position: -webkit-sticky;
bottom: 0px;
padding-bottom: 10px;
margin-top:77vh;
background-color: darkgrey;
font-size: 12px;
font-weight: 600;
text-align: right;
color: black;
border-top: 3px solid black;
}

.total h2 {
margin-right: 10px;
margin-top: 10px;
}

.cartclose{
position: absolute;
font-size: 1rem;
font-weight: bold;
width: 1rem;
background-color: darkgray;
border: 1px dotted;
top:3.8rem;
right: 0.5rem;
cursor: pointer;
margin-top: 30px;
}

.cartempty{
color: white;
font-weight: bold;
justify-content: center;
text-align: center;
}


